<template>
  <div class="section">
    <div class="tabs">
      <div :class="{active:tab == 1}" @click="tab = 1">事故复杂率</div>
      <div :class="{active:tab == 2}" @click="tab = 2">事故复杂表</div>
    </div>
    <div class="map flex" v-show="tab == 1">
      <div id="pie" ></div>
      <div class="bar">
        <div id="bar1" style="height: 50%;"></div>
        <div id="bar2" style="height: 50%;"></div>
      </div>
    </div>
    <el-table v-show="tab == 2" :data="list" border style="width: 100%">
        <el-table-column prop="" label="侧钻">
        </el-table-column>
        <el-table-column prop="" label="油套管问题">
        </el-table-column>
        <el-table-column prop="" label="修理">
        </el-table-column>
        <el-table-column prop="" label="定向仪器故障">
        </el-table-column>
        <el-table-column prop="" label="井口装置问题">
        </el-table-column>
        <el-table-column prop="" label="非计划起钻">
        </el-table-column>
        <el-table-column prop="" label="组织停工">
        </el-table-column>
        <el-table-column prop="" label="等人员">
        </el-table-column>
        <el-table-column prop="" label="等设备">
        </el-table-column>
          <el-table-column prop="" label="其它">
        </el-table-column>
          <el-table-column prop="" label="卡钻/起下钻遇阻">
        </el-table-column>
          <el-table-column prop="" label="卡测井仪器">
        </el-table-column>
          <el-table-column prop="" label="落鱼处理">
        </el-table-column>
          <el-table-column prop="" label="井漏">
        </el-table-column>
          <el-table-column prop="" label="井眼问题">
        </el-table-column>
          <el-table-column prop="" label="固井问题">
        </el-table-column>
          <el-table-column prop="" label="井控复杂">
        </el-table-column>
    </el-table>
     
  </div>
</template>

<script>
import echarts from 'echarts'
import request from '@/utils/request'
export default {
  props: ["route"],
  data () {
     return {
        tab: 1,
        list: [],
      }
  },

  components: {},

  computed: {},

  mounted: function() {
    let data = this.route;
    request({
      url: '/singlewell/complexcondition?well_no=test&well_hole=hole1',
      method: 'get',
      data
    }).then(res => {
      console.log(res);
    })
    this.draw()
  },

  methods: {
    draw() {
      this.pie();
      this.bar()
    },
    pie() {
      let pie = echarts.init(document.getElementById('pie'));
        pie.setOption({
            tooltip : {
                trigger: 'item',
                formatter: "{a} <br/>{b} : {c} ({d}%)"
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            series : [
               {
                name: '访问来源',
                type: 'pie',
                radius : ['38%','50%'],
                center: ['46%', '26%'],
                data:[
                  {value:335, name:'直接访问'},
                  {value:310, name:'邮件营销'},
                  {value:234, name:'联盟广告'},
                  {value:135, name:'视频广告'},
                  {value:1548, name:'搜索引擎'}
                ]
              },
              {
                name: '事故复杂率',
                type: 'pie',
                radius : '30%',
                center: ['46%', '26%'],
                label: {
                  normal: {
                    position: 'inner'
                  }
                },
                data:[
                  {value:335, name:'直接访问'},
                  {value:310, name:'邮件营销'},
                  {value:234, name:'联盟广告'},
                  {value:135, name:'视频广告'},
                  {value:1548, name:'搜索引擎'}
                ]
              },
              {
                name: '访问来源',
                type: 'pie',
                radius : ['38%','50%'],
                center: ['46%', '78%'],
                data:[
                  {value:335, name:'直接访问'},
                  {value:310, name:'邮件营销'},
                  {value:234, name:'联盟广告'},
                  {value:135, name:'视频广告'},
                  {value:1548, name:'搜索引擎'}
                ]
              },
              {
                name: '事故复杂率',
                type: 'pie',
                radius : '30%',
                center: ['46%', '78%'],
                label: {
                  normal: {
                    position: 'inner'
                  }
                },
                data:[
                  {value:335, name:'直接访问'},
                  {value:310, name:'邮件营销'},
                  {value:234, name:'联盟广告'},
                  {value:135, name:'视频广告'},
                  {value:1548, name:'搜索引擎'}
                ]
              }
            ]
        })
    },
    bar() {
      let bar1 = echarts.init(document.getElementById('bar1'));
      let bar2 = echarts.init(document.getElementById('bar2'));
      let option = {
        color: ['#3398DB'],
        tooltip : {
          trigger: 'axis',
          axisPointer : {            // 坐标轴指示器，坐标轴触发有效
            type : 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        grid: {
          left: '3%',
          right: '3%',
          bottom: '12%',
          containLabel: true
        },
        xAxis : [
          {
            type : 'category',
            data : ['移井架/搬迁', '油套管问题', '拆/装BOP及试压', '接钻具', '井控复杂', '井控复杂', '钻套管附件和水泥塞','移井架/搬迁', '油套管问题', '拆/装BOP及试压', '接钻具', '井控复杂', '井控复杂', '钻套管附件和水泥塞'],
          }
        ],
        yAxis : [
          {
            type : 'value'
          }
        ],
        dataZoom: [
          {
            type: 'inside'
          },
          {
            type: 'slider'
          }
        ],
        series : [
          {
            name:'直接访问',
            type:'bar',
            barWidth: '60%',
            center: ['75%', '60%'],
            data:[10, 52, 200, 334, 390, 330, 220,10, 52, 200, 334, 390, 330, 220]
          }
        ]
      }
      bar1.setOption({
        ...option,
        title: {
          text: '事故复杂占生产时效比率',
        }
      });
      bar2.setOption({
        ...option,
        title: {
          text: '事故复杂占非生产时效比率',
        }
      })
    }
  }
}
</script>

<style lang='scss' scoped>
.map {
  height: calc(100% - 50px);
  background: #fff;
  overflow: auto;
}
#pie {
  width: 500px;
}
.bar {
  width: calc(100% - 500px);
}
</style>




